import Loading from "../../../../Loader/Loading";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import style from "../../../FinanceManager/FeesStructure/FeesStructure.module.css";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import HostelSelectFeeStructureCard from "./HostelSelectFeeStructureCard";
import CreateButton from "../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import { admissionIntakeType } from "../../../../Constant/Admission/admission_constant";
import TileWrapper from "../../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../../Ui/Tab/Tile/TileCard";
import { useHostelFeeStructureList } from "../../../../Hooks/ApiHooks/HostelManager/hostel-manager-api";
const HostelSelectFeeStructure = ({
  onClose,
  hid,
  bid,
  cmid,
  selectedStructure,
  onSelectedStrucutre,
  title,
  oneStep,
  isAdmissionIntake,
  isOngoing,
  unitId,
}) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [selected, setSelected] = useState("");
  const [extra, setExtra] = useState({
    intake_type: "",
  });
  const [errorField, setErrorField] = useState("");

  const { hostelFeeStructureList, hostelFeeStructureListLoading } =
    useHostelFeeStructureList({
      data: {
        hid: hid,
        page: page,
        limit: 10,
        search: search,
        filterBy: cmid,
        batchBy: bid,
        unit_by: unitId ? unitId : "",
      },
      skip: !hid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (hostelFeeStructureList?.list?.length) {
      let obj = nextPage(hostelFeeStructureList?.list?.length);
      setState(() => obj);
    }
  }, [hostelFeeStructureList?.list?.length]);

  const onSearchEvent = (e) => {
    if (
      !hostelFeeStructureListLoading &&
      hostelFeeStructureList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onSelect = (data) => {
    if (oneStep) {
      onSelectedStrucutre(data);
    }
    setSelected(data);
  };

  const onTile = (val) => {
    setExtra((prev) => ({
      ...prev,
      intake_type: val,
    }));
  };
  const onConfirm = () => {
    if (isAdmissionIntake) {
      if (!extra.intake_type) {
        setErrorField({ intake_type: "* required" });
      } else {
        onSelectedStrucutre(selected, extra.intake_type);
      }
    } else {
      onSelectedStrucutre(selected, extra.intake_type);
    }
  };
  return (
    <>
      {!selected ? (
        <Modal onClose={onClose}>
          <div
            className={style.modal_container}
            style={{
              maxWidth: "40rem",
            }}
          >
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t(title ?? "edit_fees_structure")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0.3rem",
              }}
            >
              <div
                className={style.fm_search_full_container_inner}
                style={{
                  marginBottom: "0.6rem",
                }}
              >
                <input
                  type="text"
                  placeholder={t("search")}
                  className={style.fm_search_full_container_inner}
                  onChange={onSearchEvent}
                  onKeyUp={onCallBindSearch}
                  value={searchCopy}
                />
                <img
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                  alt="search icon"
                  className={style.fm_search_full_icon}
                />
              </div>
              {hostelFeeStructureList?.list?.map((structure, index) =>
                hostelFeeStructureList?.list?.length === index + 1 ? (
                  <div key={structure?._id} ref={ref}>
                    <HostelSelectFeeStructureCard
                      structure={structure}
                      selectedStructure={selectedStructure}
                      onSelect={onSelect}
                    />
                  </div>
                ) : (
                  <HostelSelectFeeStructureCard
                    structure={structure}
                    key={structure?._id}
                    selectedStructure={selectedStructure}
                    onSelect={onSelect}
                  />
                )
              )}

              {hostelFeeStructureListLoading ? (
                search ? (
                  <QvipleLoader />
                ) : (
                  <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
                )
              ) : search ? (
                hostelFeeStructureList?.list?.length === 0 && (
                  <EmptyMessage
                    customStyleContainer={{
                      marginTop: "1.5rem",
                    }}
                    title="No fee structure found related this search."
                  />
                )
              ) : (
                hostelFeeStructureList?.list?.length === 0 && (
                  <EmptyMessage
                    customStyleContainer={{
                      marginTop: "1.5rem",
                    }}
                    title="No fee structure added."
                  />
                )
              )}
            </UserMemberContentWrapper>
          </div>
        </Modal>
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("fee_structure")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={() => onSelect("")}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            {isAdmissionIntake
              ? isOngoing && (
                  <UserMemberContentWrapper
                    customStyle={{
                      paddingTop: "0.3rem",
                    }}
                  >
                    <TileWrapper
                      label="admission_intake_type"
                      errorShow={errorField["intake_type"]}
                    >
                      {admissionIntakeType?.map((tile) => (
                        <TileCard
                          key={tile?.key}
                          tile={tile}
                          onTile={onTile}
                          activeTile={extra.intake_type}
                        />
                      ))}
                    </TileWrapper>
                  </UserMemberContentWrapper>
                )
              : null}

            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <div className={style.fee_middle_section}>
                <p>{t("fee_structure_of_student_is")}</p>
                <h6>{selected?.category_master?.category_name}</h6>
                <p>
                  {" "}
                  {t("total_admission_fees")} {" : "} {t("rs")}{" "}
                  <span
                    style={{
                      fontWeight: "600",
                    }}
                  >
                    {selected?.total_admission_fees ?? 0}
                  </span>
                </p>
                <p>
                  {t("applicable_fees_amount")} {" : "} {t("rs")}{" "}
                  <span
                    style={{
                      fontWeight: "600",
                    }}
                  >
                    {selected?.applicable_fees ?? 0}
                  </span>
                </p>
              </div>
              <CreateButton label="confirm" onAction={onConfirm} />
            </UserMemberContentWrapper>
          </div>
        </Modal>
      )}
    </>
  );
};

export default HostelSelectFeeStructure;
